
import { supabase } from "@/lib/supabase"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"

// Disable caching
export const revalidate = 0;

export default async function TrendsPage() {
    const { data: channels, error } = await supabase
        .from('channels')
        .select('*')
        .order('created_at', { ascending: false });

    if (error) {
        console.error("Error fetching channels:", error);
    }

    return (
        <div className="space-y-8">
            <header>
                <h2 className="text-3xl font-bold tracking-tight">Top Channels</h2>
                <p className="text-muted-foreground mt-1">
                    Channels being tracked by the analyzer.
                </p>
            </header>

            <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
                {channels?.map((channel) => (
                    <Card key={channel.id} className="hover:shadow-md transition-shadow">
                        <CardHeader className="flex flex-row items-center gap-4">
                            <Avatar className="h-12 w-12">
                                <AvatarImage src={channel.thumbnail_url} alt={channel.title} />
                                <AvatarFallback>{channel.title[0]}</AvatarFallback>
                            </Avatar>
                            <div className="space-y-1">
                                <CardTitle className="text-base">{channel.title}</CardTitle>
                                <CardDescription className="line-clamp-1 break-all">
                                    {channel.custom_url || "No custom URL"}
                                </CardDescription>
                            </div>
                        </CardHeader>
                        <CardContent>
                            <div className="text-sm text-muted-foreground">
                                Displaying channel stats here...
                            </div>
                        </CardContent>
                    </Card>
                ))}
            </div>
        </div>
    )
}
